import { defineStore } from "pinia";
import { computed, ref } from "vue";

export const asideStore = defineStore("setAside", () => {
  const aside = ref(false);

  const isOpen = computed(() => aside.value);

  const openAside = () => {
    aside.value = true;
  };

  const closeAside = () => {
    aside.value = false;
  };

  return {
    aside,
    isOpen,
    openAside,
    closeAside
  };
});
