            </RouterLink>
          </div>
          <ul>
            <li class="nav-tab" @click="onToggleTab">
              <a>
                <span>메뉴소개</span>
                <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
              </a>
              <div class="dropdown">
                <ul>
                  <li><RouterLink to="/menu/burger">버거</RouterLink></li>
                  <li><RouterLink to="/menu/salad">샐러드</RouterLink></li>
                  <li><RouterLink to="/menu/dessert">디저트</RouterLink></li>
                </ul>
                <ul>
                  <li><RouterLink to="/menu/side">사이드</RouterLink></li>
                  <li><RouterLink to="/menu/drink">커피&음료</RouterLink></li>
                </ul>
              </div>
            </li>
            <li class="nav-tab" @click="onToggleTab">
              <a>
                <span>매장안내</span>
                <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
              </a>
              <div class="dropdown">
                <ul>
                  <li><RouterLink to="/store">매장찾기</RouterLink></li>
                  <li><RouterLink to="/store/join">입점/개설 문의</RouterLink></li>
                </ul>
              </div>
            </li>
            <li class="nav-tab" @click="onToggleTab">
              <a>
                <span>슈몬라이브</span>
                <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
              </a>
              <div class="dropdown">
                <ul>
                  <li><RouterLink to="/live">슈몬 News</RouterLink></li>
                  <li><RouterLink to="/live/story">슈몬 Story</RouterLink></li>
                </ul>
              </div>
            </li>
            <li class="nav-tab" @click="onToggleTab">
              <a>
                <span>브랜드소개</span>
                <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
              </a>
              <div class="dropdown">
                <ul>
                  <li><RouterLink to="/brand">브랜드컨셉</RouterLink></li>
                  <li><RouterLink to="/brand/philosophy">운영철학</RouterLink></li>
                  <li class="collaboration">
                    <span>Collaboration</span>
                    <RouterLink to="/brand/collabo">원두 콜라보</RouterLink>
                  </li>
                </ul>
              </div>
            </li>
            <li class="nav-tab">
              <RouterLink to="/quality">품질중심경영</RouterLink>
            </li>
          </ul>
        </nav>
      </div>
  </aside>
</template>

<script setup>
import { asideStore } from "@/stores/aside";
import { computed } from 'vue'

const onToggleTab = (e) => {
  const tabs = document.querySelectorAll("aside .nav-tab");

  if (e.currentTarget.classList.contains("on")) {
    e.currentTarget.classList.remove("on");
  } else {
    for (let tab of tabs) {
      if (tab !== e.currentTarget) {
        tab.classList.remove("on");
      }
      e.currentTarget.classList.add("on");
    }
  }
}

</script>

<style lang="scss" scoped>
@import "@/styles/Aside";
</style>
