<template>
  <section id="home-recommend">
    <div class="section-inner">
      <div class="section-title">
        <img src="@/assets/images/symbol.svg" alt="슈퍼몬스터 심볼">
        <h2>슈몬 추천메뉴</h2>
      </div>
      <swiper :slidesPerView="'auto'"
              :spaceBetween="12"
              :freeMode="true"
              :pagination="pagination"
              :breakpoints="{
                768: {
                  spaceBetween: 14
                },
                1200: {
                  spaceBetween: 16
                }
              }"
              :modules="swiperModules">
        <swiper-slide v-for="item in recommendedItem" @click="menuPopup().openPopup(item.id)">
          <div class="image-wrapper"></div>
          <span class="name">{{item.nameKo}}</span>
          <span class="price">{{item.price.toLocaleString()}}</span>
        </swiper-slide>
      </swiper>
      <div class="swiper-pagination"></div>
      <RouterLink class="more" to="/menu/burger">더 보기</RouterLink>
    </div>
  </section>
</template>

<script setup>
import { computed } from "vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import { FreeMode, Pagination } from "swiper/modules";
import "swiper/css";

import { menuPopup } from "@/stores/menuPopup";

const pagination = {
  el: "#home-recommend > .section-inner > .swiper-pagination",
  type: "progressbar"
}

const swiperModules = [ FreeMode, Pagination ];

const recommendedItem = computed(() => {
  return menuPopup().menuList.filter((item) => item.recommend === "Y");
});
</script>
