import { defineStore } from "pinia";
import { computed, ref } from "vue";
import burgers from "@/data/menuBurgers.json";

export const popupStore = defineStore("setPopup", () => {
  const popup = ref(false);

  const isOpen = computed(() => popup.value);
  const targetItem = ref({});

  const openPopup = (id) => {
    let burger = burgers.filter(item => item.id === id);
    if (burger.length > 0) targetItem.value = burger[0];
    popup.value = true;
  }

  const closePopup = () => {
    popup.value = false;
  }

  return {
    popup,
    isOpen,
    openPopup,
    closePopup,
    targetItem
  }
});
