import { createRouter, createWebHistory } from "vue-router";
import Home from "@/views/Home.vue";
import Menu from "@/views/Menu.vue";
import MenuBurger from "@/views/MenuBurger.vue";
import MenuSide from "@/views/MenuSide.vue";
import MenuSalad from "@/views/MenuSalad.vue";
import MenuDrink from "@/views/MenuDrink.vue";
import MenuDessert from "@/views/MenuDessert.vue";
import Store from "@/views/Store.vue";
import StoreFind from "@/views/StoreFind.vue";
import StoreJoin from "@/views/StoreJoin.vue";
import StoreFind_1 from '@/views/StoreFind_1.vue'
import StoreFind_2 from '@/views/StoreFind_2.vue'


const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/menu",
    name: "menu",
    component: Menu,
    children: [
      {
        path: "burger",
        name: "menuBurger",
        component: MenuBurger
      },
      {
        path: "side",
        name: "menuSide",
        component: MenuSide
      },
      {
        path: "salad",
        name: "menuSalad",
        component: MenuSalad
      },
      {
        path: "drink",
        name: "menuDrink",
        component: MenuDrink
      },
      {
        path: "dessert",
        name: "menuDessert",
        component: MenuDessert
      }
    ]
  },
  {
    path: "/store",
    name: "store",
    component: Store,
    children: [
      {
        path: "find",
        name: "find",
        component: StoreFind,
        children: [
          {
            path: "1",
            name: "1",
            component: StoreFind_1
          },
          {
            path: "2",
            name: "2",
            component: StoreFind_2
          }
        ]
      },
      {
        path: "join",
        name: "join",
        component: StoreJoin
      }
    ]
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
