<template>
  <main id="store">
    <div class="page-head">
      <h1 class="category">{{category}}</h1>
    </div>
    <RouterView />
  </main>
</template>

<script setup>
import { useRoute } from "vue-router";
import { computed } from 'vue'

const category = computed(() => {
  const route = useRoute();

  if (route.name === "join") {
    return "입점/개설문의";
  } else {
    return "매장찾기";
  }
})
</script>

<style lang="scss">
@import "@/styles/Store";
</style>
