import { defineStore } from "pinia";
import { computed, ref } from "vue";
import burgers from "@/data/menuBurgers.json";
import sides from "@/data/menuSides.json";


export const menuPopup = defineStore("popupStore", () => {

  const menuList = [...burgers, ...sides];

  const popup = ref(false);

  const isOpen = computed(() => popup.value);

  const targetedItem = ref({});

  const openPopup = (id) => {
    let item = menuList.filter(item => item.id === id);
    if (item.length > 0) targetedItem.value = item[0];
    popup.value = true;
  };

  const closePopup = () => {
    popup.value = false;
  };

  return {
    burgers,
    sides,
    menuList,
    popup,
    isOpen,
    targetedItem,
    openPopup,
    closePopup
  };
});
